import { DateTime } from "luxon"

const months = [
    'Janur', 'Februr', 'Mrcius', 
    'prilis', 'Mjus', 'Jnius', 
    'Jlius', 'Augusztus', 'Szeptember', 
    'Oktber', 'November', 'December'
]

const days = [
    'Htf', 'Kedd', 'Szerda', 
    'Cstrtk', 'Pntek', 'Szombat',
    'Vasrnap'
]

/**
 * 
 * @param {DateTime} dt 
 */
const getMonthName = dt => months[dt.month - 1]

const getDayName = dt => days[dt.weekday - 1]

const calculateWorkdays = () => {
    let dt = DateTime.local().plus({ days: 1 }).startOf('day')

    const days = []

    let i = 0
    while(i < 20){
        if(dt.weekday !== 6 && dt.weekday !== 7){
            days.push(dt.toISO())
            i++
        }
        dt = dt.plus({ days: 1 })
    }

    return days
}

export { getMonthName, getDayName, calculateWorkdays }